.season {
	&-wrap {
		// max-height: 100%;
		// overflow: hidden;
		background-position: center;
		background-size: cover;
		background-attachment: fixed;

		@include media-breakpoint-down(md) {
			background: none !important;
		}
	}

	&-inner {
		display: flex;
		flex-wrap: wrap;
	}

	&-details {
		width: 100%;
		position: relative;

		@include media-breakpoint-up(lg) {
			width: 40%;
		}

		.stick {
			position: sticky;
			top: 0;
			@include media-breakpoint-up(lg) {
				&:after {
					content: '';
					position: absolute;
					bottom: -50px;
					left: -50px;
					width: 100vw;
					background: linear-gradient(
						0deg,
						rgba(0, 0, 0, 1) 30%,
						rgba(0, 0, 0, 0.700717787114846) 65%,
						rgba(0, 0, 0, 0) 100%
					);
					height: 60vh;
					z-index: -1;
				}
			}

			@include media-breakpoint-up(lg) {
				height: calc(100vh - 100px);
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
			}

			.main-title {
				margin-bottom: 10px;

				@include media-breakpoint-up(lg) {
					margin-bottom: 0;
				}
			}

			.btn {
				margin-bottom: 20px;
			}
		}

		.overview {
			max-width: 80%;
			font-size: 12px;
			line-height: 16px;
			display: none;

			@include media-breakpoint-up(lg) {
				display: block;
			}
		}
	}

	&-episodes {
		width: 100%;

		@include media-breakpoint-up(lg) {
			width: 60%;
			padding-left: 50px;
		}
	}

	&-episode {
		display: flex;
		background: rgba($black, 1);

		margin-bottom: 20px;
		position: relative;
		padding-right: 4px;
		flex-direction: column;
		// cursor: alias;

		@include media-breakpoint-up(lg) {
			flex-direction: row;
			min-height: 120px;
			max-height: 120px;
		}

		&.not-aired {
			opacity: 0.8;

			img {
				opacity: 0;
			}
		}

		&:after {
			content: '';
			background: $dark-grey;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			width: 4px;
			opacity: 1;
			transition: all 0.2s ease 0.2s;
		}

		&--actions {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			opacity: 0;
			transition: opacity 0.2s ease;
			z-index: 4;
			pointer-events: none;

			.btn {
				margin: 0 5px;
			}
		}

		// &:hover {
		// 	.season-episode--actions {
		// 		opacity: 1;
		// 		transition: opacity 0.2s ease 0.2s;
		// 		pointer-events: all;
		// 	}

		// 	&:after {
		// 		width: 100%;
		// 		opacity: 0.8;
		// 		transition: all 0.2s ease;
		// 	}
		// }

		&.on-server {
			&:after {
				background: $good;
			}
		}

		&--img {
			width: 150px;
			min-width: 150px;
			height: 120px;
			min-height: 100px;
			position: relative;
			display: none;

			@include media-breakpoint-up(lg) {
				display: block;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&--info {
			padding: 10px;
			display: flex;
			justify-content: center;
			flex-direction: column;

			p {
				margin: 0;

				&.detail {
					max-height: calc(14px * 3);
					overflow: hidden;
				}
			}
		}
	}
}
